





import Vue from 'vue'
import { Map
//, LngLatBounds, LngLat, Popup, NavigationControl 
} from 'mapbox-gl'
import style from './resources/mapboxstyle'

export default Vue.extend({
    data: () => ({
        map: {} as Map
    }),
    mounted() {
        const map = this.map = new Map({
            container: this.$refs.mymap as HTMLElement,
            style
        })

        map.on('load', ()=>{
            console.log('loaded!')
        })
    }

})
